<script>
import { APIURLs } from '@/API/Constants';
export default {
    data: () => ({
      Orders:[],
      dataloading:true,
        count:0,
      valid: false,
      firstname: '1',
      lastname: '2',
      nameRules: [
        value => {
          if (value) return true

          return 'Name is required.'
        },
        value => {
          if (value?.length <= 10) return true

          return 'Name must be less than 10 characters.'
        },
        value=>{
            var num=Number(value);
            if(num<=1000) return true
            return "Name must be less than 1000"
        }
      ],
      email: '',
      emailRules: [
        value => {
          if (value) return true

          return 'E-mail is required.'
        },
        value => {
          if (/.+@.+\..+/.test(value)) return true

          return 'E-mail must be valid.'
        },
      ]
    }),
    created(){
      this.getOrders();
    },
    methods:{
       async getOrders(){
         await fetch(APIURLs.APIs.find(a=>{return a.name=="test"}).url)
          .then(resp=>resp.json())
          .then(data=>{this.Orders=data; this.dataloading=false})
        }
      }
  }
</script>
<template >
  <div>
    <v-row>
      <v-col>
        <v-btn color="primary" @click="count++">You clicked me {{ count }} times.</v-btn>
      </v-col>
    </v-row>
  </div>
  <v-form v-model="valid">
    <v-container>
      <v-row>
        <v-col
          cols="12"
          md="4"
        >
          <v-text-field
            v-model="firstname"
            :counter="10"
            :rules="nameRules"
            label="First name"
            required
          ></v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="4"
        >
          <v-text-field
            v-model="lastname"
            :counter="10"
            :rules="nameRules"
            label="Last name"
            required
          ></v-text-field>
        </v-col>

        <v-col
          cols="12"
          md="4"
        >
          <v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail"
            required
          ></v-text-field>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
  <hr/>
  <v-data-table
    :items="Orders"
    item-key="id"
    items-per-page="5"
    :loading="dataloading"
  ></v-data-table>
</template>

